"use client";
import { List } from "@/components/animelist";
import { Button } from "@/components/ui/button";
import { useState } from "react";

export default function Page() {
  const [page, setPage] = useState(1);
  const [letter, setLetter] = useState("all");
  const list = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
  list.splice(0, 0, "0-9");
  return (
    <div className="w-full flex flex-col md:my-12 my-8 md:mx-12 mx-4 gap-12 overflow-x-hidden">
      <div className="flex flex-wrap items-center gap-4">
        {list.map((item, index) => (
          <Button
            className="text-xs max-w-12 w-full h-auto shadow-none aspect-square flex-auto"
            variant={"outline"}
            size={"sm"}
          >
            {item}
          </Button>
        ))}
      </div>
      <List page={page} letter={letter} />z{" "}
    </div>
  );
}
